import styles from "./Menu.module.css";
import cn from "classnames";
import { useState } from "react";
import MenuSVG from "../../images/menu.svg"
import {Button} from "../Button/Button";
import buttonStyle from "../Button/Button.module.css";
import {Link} from "react-router-dom";
import header from "../../templates/Header/Header.module.css";
export const Menu = ({
  children,
  className,
  ...props
}) => {

    const [isClicked, setClicked] = useState(false);

    const handleIsClicked = () => {
        setClicked(!isClicked)
    }
    const user = sessionStorage.getItem('user');


  return(
      <>
          <img src={MenuSVG} alt={"menu"} width={40} height={40} onClick={handleIsClicked}
          className={cn({
              [styles.img]: !isClicked,
              [styles.img_clicked]: isClicked
          })}/>
          {isClicked ?
            <>
                <div className={styles.menu}>
                    <Link to={user ? "/catalog" : "/login"} className={header.link}>
                        <Button state={"default"} type={"primary"} className={cn(buttonStyle.button, styles.addButton)}>Каталог</Button>
                    </Link>
                    <Link to={user ? "/advert" : "/login"} className={header.link}>
                        <Button state={"default"} type={"primary"} className={cn(buttonStyle.button, styles.addButton)}>{user ? "Добавить статью" : "Войти"}</Button>
                    </Link>
                </div>
            </>
          : null}
      </>
  )
};
